.section {
  padding: 0px 36px;
}

.wrapper {
  display: flex;
  align-items: flex-start;
  gap: 20px;
}

.title {
  font-size: 20px;
  text-transform: capitalize;
}

.title span {
  color: red;
}

.score {
  font-size: 14px;
  margin-top: 16px;
  font-weight: 500;
}

.score span {
  color: green;
}

.overview_title,
.genres {
  font-size: 18px;
  margin-top: 16px;
  text-transform: capitalize;
}

.overview__box {
  padding: 8px;
  width: 600px;
  height: 120px;
  overflow-y: auto;
  margin-top: 6px;
  font-weight: 400;
}

.genres_list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 12px;
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: 500;
  gap: 12px;
}
